@mixin cursor($type: default) {
	@if ($type == 'default') {
		@include cursor-custom($cursor-default, $cursor-default-x, $cursor-default-y);
	}

	@if ($type == 'pointer') {
		@include transition(all);

		&:hover {
			@include cursor-custom($cursor-pointer, $cursor-pointer-x, $cursor-pointer-y, $default: pointer);
		}
	}
}
